import { Component, Input, Output, OnInit, EventEmitter, ViewEncapsulation } from '@angular/core';
import { SafeUrl } from '../../pipes/index';
@Component({
  selector: 'logo',
  template: `
      <header class="landing-page-header" *ngIf="data.visible" >
        <div class=" logo">
          <a 
            target="_blank"
            [href]="redirectUrl() |safeUrl">
            <img src="{{data.props.title}}" alt="{{data.config.placeholder}}">
          </a>
        </div>
      </header>
  `,
  encapsulation: ViewEncapsulation.None
})
export class Logo implements OnInit {
  @Input() data: any;
  value: string;
  redirectUrl() {
    return (this.data.config.direction.length > this.data.config.direction.split('/')[0].length + 2)?this.data.config.direction:'javascript:void(0)';
  }
  ngOnInit() { }
}



